<template>
  <div class="circle">
    <div class="circle__wrapper">
      <div class="circle__back">
      </div>
      <div class="circle__left__wrapper">
        <div class="circle__left" :style="{'transform': 'rotate('+ rotateLeft() +'deg)','border-top-color': color,'border-left-color': color }">
        </div>
      </div>
      <div class="circle__right__wrapper">
        <div class="circle__right" :style="{'transform': 'rotate('+ rotateRight() +'deg)',
             'border-top-color': color,
             'border-right-color':color
            }">
        </div>
      </div>
      <slot></slot>
    </div>
    <div class="circle__title">{{text}}</div>
  </div>

</template>
<script>
  export default {
    props: ["text", "percent","color"],
    methods: {
      rotateLeft: function () {
        if(this.percent > 0.5){
          return 135 +  (this.percent - 0.5) * 360
        }
        return 135
      },
      rotateRight: function () {
        if(this.percent < 0.5){
          return -135   + this.percent * 360
        }
        return 45
      }
    }
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  .circle {
    flex: 1;
    padding: .3rem .3rem;
    &__wrapper {
      width: 2.8rem;
      height: 2.8rem;
      line-height: 2.8rem;
      position: relative;
      margin: auto;
      font-size: 1rem;
      text-align: center
    }
    &__back {
      width: 2.8rem;
      height: 2.8rem;
      position: absolute;
      top: 0;
      left: 0;
      border: .2rem solid rgba(0,0,0,0.2);
      border-radius: 50%;
    }
    &__left__wrapper {
      width: 1.4rem;
      height: 2.8rem;
      position: absolute;
      left: 0;
      overflow: hidden;
    }
    &__left {
      width: 2.8rem;
      height: 2.8rem;
      position: absolute;
      top: 0;
      left: 0;
      border: .2rem solid transparent;
      border-radius: 50%;
    }
    &__right {
      width: 2.8rem;
      height: 2.8rem;
      position: absolute;
      top: 0;
      right: 0;
      border: .2rem solid transparent;
      border-radius: 50%;
    }
    &__right__wrapper {
      width: 1.4rem;
      height: 2.8rem;
      position: absolute;
      right: 0;
      overflow: hidden;
    }
    &__title{
      text-align: center
    }
  }
</style>



