<template>
  <div class="xa-picker__group" @touchstart="ts($event)" @touchmove.prevent.stop="tm($event)" @touchend="te($event)">
    <div class="xa-picker__mask"></div>
    <div class="xa-picker__indicator"></div>
    <div class="xa-picker__content">

      <div v-if="item.text" v-for="item in items" class="xa-picker__item">{{item.text}}</div>

    </div>
  </div>
</template>

<script>
  export default{
    data(){
      return {
        /**偏移位置**/
        offset: 3 * 25 * 1.4, //默认的留出上面2个
        /** 滑动相关**/
        start: null,
        end: null,
        startTime: null,
        startOffset: 0,
        points: []
      }
    },
    props: ['items','default'],
    methods: {
      _cellHeight(){
        return 25 * 1.4
      },
      _start(pageY){
        this.startOffset = this.offset;
        this.start = pageY;
        this.startTime = +new Date();
        this._setTransition(0)
      },
      _move(pageY){
        this.end = pageY;
        const diff = this.end - this.start;
        this.offset = this.startOffset + diff
        this._setTransform(this.offset)
        this.startTime = +new Date();
        this.points.push({time: this.startTime, y: this.end});
        if (this.points.length > 40) {
          this.points.shift();
        }
      },
      _end(pageY,){
        if (!this.start) return;
        /**
         * 思路:
         * 0. touchstart 记录按下的点和时间
         * 1. touchmove 移动时记录前 40个经过的点和时间
         * 2. touchend 松开手时, 记录该点和时间. 如果松开手时的时间, 距离上一次 move时的时间超过 100ms, 那么认为停止了, 不执行惯性滑动
         *    如果间隔时间在 100ms 内, 查找 100ms 内最近的那个点, 和松开手时的那个点, 计算距离和时间差, 算出速度
         *    速度乘以惯性滑动的时间, 例如 300ms, 计算出应该滑动的距离
         */
        const endTime = +new Date()
        const relativeY = window.innerHeight - (35 * 7 / 2);
        this.end = pageY;

        // 如果上次时间距离松开手的时间超过 100ms, 则停止了, 没有惯性滑动
        if (endTime - this.startTime > 100) {
          if (Math.abs(this.end - this.start) > 10) {
            this._stop(this.end - this.start);
          } else {
            this._stop(relativeY - this.end);
          }
        } else {
          if (Math.abs(this.end - this.start) > 10) {
            const endPos = this.points.length - 1;
            let startPos = endPos;
            for (let i = endPos; i > 0 && this.startTime - this.points[i].time < 100; i--) {
              startPos = i;
            }

            if (startPos !== endPos) {
              const ep = this.points[endPos];
              const sp = this.points[startPos];
              const t = ep.time - sp.time;
              const s = ep.y - sp.y;
              const v = s / t; // 出手时的速度
              const diff = v * 150 + (this.end - this.start); // 滑行 150ms,这里直接影响“灵敏度”
              this._stop(diff);
            }
            else {
              this._stop(0);
            }
          } else {
            this._stop(relativeY - this.end);
          }
        }

        this.start = null;
      },
      _stop(diff){
        const cellHeight = this._cellHeight()
        this.offset = this.startOffset + diff


        //限定范围
        const max = 3 * cellHeight   //max选中第一格,上面空2格
        const min = max - (this.items.length - 1) * cellHeight
        if (this.offset > max) {
          this.offset = max
        }
        if (this.offset < min) {
          this.offset = min
        }

        //跳到最接近的元素
        this.offset = Math.round(this.offset / cellHeight) * cellHeight

        //        // 如果是 disabled 的就跳过
//        let index = defaults.offset - translate / defaults.rowHeight;
//        while (!!defaults.items[index] && defaults.items[index].disabled) {
//          diff > 0 ? ++index : --index;
//        }

        this._setTransition(0.3)
        this._setTransform(this.offset)
        //触发事件
        this.$emit('select', (3 - Math.round(this.offset / cellHeight)))
      },
      _setTransform(offset){
        this.$el.querySelectorAll(".xa-picker__content")[0].style.transform = 'translate3d(0,' + offset + 'px' + ',0)'
      },
      _setTransition(time){
        this.$el.querySelectorAll(".xa-picker__content")[0].style.transition = 'all ' + time + 's'
      },
      ts: function (e) {
        this._start(e.changedTouches[0].pageY)
      },
      tm: function (e) {
        this._move(e.changedTouches[0].pageY)
      },
      te: function (e) {
        this._end(e.changedTouches[0].pageY)
      },
      select: function (index) {
        this.offset = -(index - 3) * this._cellHeight()
        this._setTransition(0.3)
        this._setTransform(this.offset)
      }
    },
    mounted: function () {
      this.select(this.default||0)
    },
    watch: {
      //如果外界修改了items,重新stop一下选中正确的位置
      items: function (val) {
        this.startOffset = this.offset
        this._stop(0)
      },

    }
  }
</script>




