<template>
  <div class="xa-page  ">
    <head-top title="航班动态" back="true">
      <div slot="right" class="xa-icon-lg" @click="selectDate = !selectDate">&#xe66c;</div>
    </head-top>
    <div>
      <div class="flight-header">
        <div class="flight-header__nav xa-nav" style="display:flex;">
          <a :class="{active: filter=='out'}" @click="filter='out'">出港</a>
          <a :class="{active: filter=='inn'}" @click="filter='inn'">进港</a>
          <a :class="{active: filter=='care'}" @click="filter='care'">关注</a>
          <a :class="{active: filter=='vip'}" @click="filter='vip'">要客</a>
        </div>
        <div class="flight-header__tr">
          <div class="flight-header__td">航班</div>
          <div class="flight-header__td">航线</div>
          <div class="flight-header__td">机位</div>
          <div class="flight-header__td">起飞</div>
          <div class="flight-header__td">到达</div>
          <div class="flight-header__td">状态</div>
        </div>
      </div>
      <scroller :on-refresh="refresh" class="flight-content">
        <a v-for="f in flightsOnShow" class="flight-content__tr" @click="viewFlightDetail(f)" style="display:flex">
          <div class="flight-content__td">{{f.FlightNo}}<br>{{f.AcReg}}</div>
          <div class="flight-content__td">{{f.Depa}}<br>{{f.Arri}}</div>
          <div class="flight-content__td">{{f.StandOut}}</div>
          <div class="flight-content__td">
            <span class="st">{{new Date(f.Std).toString("HH:mm")}}</span><br>
            <span v-if="f.Etd&&!f.Atd" class="et">{{new Date(f.Etd).toString("HH:mm")}}</span>
            <span v-if="f.Atd" class="at">{{new Date(f.Atd).toString("HH:mm")}}</span>
            <span v-if="!f.Etd&&!f.Atd">&nbsp;</span>
          </div>
          <div class="flight-content__td">
            <span class="st">{{new Date(f.Sta).toString("HH:mm")}}</span><br>
            <span v-if="f.Eta&&!f.Ata" class="et">{{new Date(f.Eta).toString("HH:mm")}}</span>
            <span v-if="f.Ata" class="at">{{new Date(f.Ata).toString("HH:mm")}}</span>
            <span v-if="!f.Eta&&!f.Ata">&nbsp;</span>
          </div>
          <div class="flight-content__td">起飞</div>
        </a>
      </scroller>
    </div>
    <footer-guide></footer-guide>
    <div v-if="selectDate" class="xa-mask" style="z-index:800" @click="selectDate=false"></div>
    <date-picker :show="selectDate" start="2017-01-01" end="2018-04-01" v-on:select="_onSelect($event)">
    </date-picker>
  </div>
</template>


<script>
  //v-bind:style="{transform: 'translate3d(0,'+ offset+'px' +',0)'}">
  import footerGuide from '@/components/footer'
  import headTop from '@/components/header'
  import datePicker from '@/components/picker/datepicker'
  import url from '@/utils/url.js'
  export default{
    data(){
      return {
        selectDate:false,
        flightsOnShow: [],
        flights: [],
        filter: 'out',



      }
    },
    methods: {
      refresh: function (done) {
        setTimeout(() => {
          done()
        }, 1500)
      },
      log: function (e) {
        console.log(e)
      },
      async getFlights() {
        this.$http.get(url('flight/date/2017-7-18')).then(response => {
          return response.json()
        }).then(json=> {
          this.flights = json
        })
      },
      viewFlightDetail: function (f) {
        this.$router.push("/flight/"+f.FlightId+"/index")
      },
      filterFlights: function (val) {
        if (!val) {
          this.flightsOnShow = this.flights;
        }
        if (val == 'out') {
          this.flightsOnShow = this.flights.filter(function (f) {
            return f.Depa == "ZSAM"
          })
        }
        if (val == 'inn') {
          this.flightsOnShow = this.flights.filter(function (f) {
            return f.Arri == "ZSAM"
          })
        }
        if (val == 'care') {
          this.flightsOnShow = this.flights.filter(function (f) {
            return f.Care
          })
        }
        if (val == 'vip') {
          this.flightsOnShow = this.flights.filter(function (f) {
            return f.Vip == 'Y'
          })
        }
      },
      _onSelect:function(date){
        console.log('flight select',date.toString("yyyy-MM-dd"))
        this.selectDate = false
      }
    },
    watch: {
      flights: function (val, oldVal) {
        this.filterFlights(this.filter)
      },
      filter: function (val, oldVal) {
        this.filterFlights(val)
      }
    },
    components: {
      footerGuide,
      headTop,
      datePicker
    },

    created: function () {
      if (this.flights.length == 0) {
        this.getFlights()
      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  @import "src/style/base.scss";

  .flight-header {
    background-color: #fff;
    margin-top: $XaHeaderHeight;
    width: 100%;
    z-index: 100;
    position: fixed;
    left: 0;
    font-size: 0.6rem;
    &__tr {
      display: flex;
      height: 1.2rem;
      line-height: 1.2rem;
      font-weight: bold;
      text-align: center;
      border-top: 1px solid #ECECEC;
      border-bottom: 1px solid #ECECEC;
    }
    &__td {
      flex: 1;
    }
  }

  .flight-content {
    font-size: 0.6rem;
    margin-top: 4.4rem;
    height: calc(100% - 4.4rem);
    width: 100%;
    text-align: center;
    background-color: #fff;
    z-index: -1;
    &__tr {
      display: flex;
      border-bottom: 1px solid #ECECEC;
      padding: .2rem 0;
      &:active {
        background-color: $XaColorLighterBlue;
      }
    }
    &__td {
      flex: 1;
      flex-grow:1; //for old version browser
      width:0; //for old version browser
    }
  }

  .st {
    color: $XaStFontColor;
  }

  .et {
    color: #994500;
  }

  .at {
    color: $XaColorDarkerBlue;
  }
</style>



