<template>
    <div class="footer-tab">
        <a v-for="menu in menus" @click.stop="$router.push(menu.url)"  :class="['tab-item',{active: $isRouteIn(menu.url)}]">
                <span class="icon xa-icon" v-html="menu.iconfont"></span>
                <span>{{menu.name}}</span>
        </a>
    </div>
</template>
<script>
    export default {
        data(){
            return {
                menus: [
                    {name: '首页', url: '/home', iconfont: '&#xe7a4;'},
                    {name: '航班', url: '/flight', iconfont: '&#xe652;'},
                    {name: '消息', url: '/message', iconfont: '&#xe65b;'},
                    {name: '更多', url: '/more', iconfont: '&#xe677;'}
                ]
            }
        },
        methods: {

        }
    }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
    @import "src/style/common/variable.scss";

    .footer-tab {
        background-color: #272822;
        position: fixed;
        z-index: 1000;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: $XaFooterHeight;
        display: flex;
        box-shadow: 0 -0.026667rem 0.053333rem rgba(0, 0, 0, .3);
        opacity: 0.96;
    }

    .tab-item {
        flex: 1;
        display: inline-block;
        text-align: center;
        font-size: .45rem;
        color: $XaTextGrayColor;
        &.active{
            color: $XaColorAirBlue;
        }
        & .icon {
            display: block;
            margin-bottom: .1rem;
            margin-top: .3rem;
            height: .8rem;
            font-size: .8rem;
        }
    }



</style>



