<template>
  <div class="xa-page">
    <head-top back="true" title="MF8401商务信息"></head-top>
    <scroller :on-refresh="refresh" ref="scroll" class="container">
      <div class="xa-panel" v-for="d in newData">
        <div class="xa-panel__hd">{{d.Departure + '-' + d.Arrival}}</div>
        <div class="xa-panel__bd">
          <div class="line">
            <div style="width:30%">订座人数 {{d.OrderNum}}</div>
            <div style="width:22%">成人 {{d.AdultNum}}</div>
            <div style="width:22%">儿童 {{d.ChildNum}}</div>
            <div style="width:22%">婴儿 {{d.InfantNum}}</div>
          </div>
          <div class="line">
            <div style="width:30%">出港人数 {{d.TotalNum}}</div>
            <div style="width:30%">商务舱 5/8</div>
            <div style="width:30%">经济舱 148/161</div>
          </div>

          <div class="line">
            <div style="width:30%">预计货物 {{d.CargoWeight+d.MailWeight}}</div>
            <div style="width:30%">货物重量 {{d.CargoWeight}}</div>
            <div style="width:30%">邮件重量 {{d.MailWeight}}</div>
          </div>


          <div class="line">
            <div style="width:30%"></div>
            <div style="width:30%">行李件数 {{d.BagNum}}</div>
            <div style="width:30%">行李重量 {{d.BagWeight}}</div>
          </div>
        </div>
        <div class="xa-panel__ft">
          <div class="weight__label">预配总重</div>
          <div class="weight__text">{{d.TotalWeight}}</div>
        </div>
      </div>
    </scroller>
  </div>
</template>
<script>
  import headTop from '@/components/header'
  export default {
    data(){
      return{
        rawData:[],
        newData:[]
      }
    },
    methods: {
      refresh: function (done) {
        var id = this.$route.params.id
        this.$http.get('http://www.easy-mock.com/mock/596f1d0ea1d30433d837b4bf/example/flight/'+id+'/business').then(response => {
          return response.json()
        }).then(json=> {
          this.rawData = json
          done()
        });
      },
    },
    watch:{
      rawData:function(val){
        console.log(val)
        this.newData = val

      }
    },
    mounted:function(){
      this.$refs.scroll.triggerPullToRefresh()
    },
    components: {
      headTop
    }
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "src/style/base.scss";

  .xa-page {
    background-color: $XaBgGrayColor;
  }

  .container {
    margin-top: $XaHeaderHeight;
  }

  .xa-panel__ft {
    text-align: right;
    display: flex;
  }

  .line {
    display: flex;
    padding: 0.2rem 0;
    & > .cell {

    }
  }

  .weight {
    &__label {
      color: $XaTextGrayColor;
      flex: 2;
      text-align: left;
    }
    &__text {
      flex: 8;
      text-align: left;
    }
  }
</style>



