<template>
  <div class="xa-page fd">
    <div class="xa-modal" v-if="showModal" @click="showOrHideMenu(false)"></div>
    <head-top back="/flight" title="MF8401">
      <div slot="right" class="xa-icon-lg">&#xe6d2;</div>
    </head-top>
    <scroller style="padding-top:2rem;" :on-refresh="refresh">
      <div class="fd-general">
        <div class="fd-general__element">
          <div class="fd-general__element__line1 fd-airport">厦门</div>
          <div class="fd-general__element__line2 fd-time">15:00</div>
          <div class="fd-general__element__line3 fd-light">计划<span>1500</span></div>
        </div>
        <div class="fd-general__element__middle">
          <div class="fd-general__element__line1 fd-light">73/24</div>
          <div class="fd-general__element__line2">
            <div style="height:2px;position:relative;background-color: #aaa;top:50%;">
              <div style="height:2px;width:45%;background-color:#C7E0F4"></div>
              <div class="icon xa-icon fd-plane">
                &#xe609;</div>
            </div>
          </div>
          <div class="fd-general__element__line3 fd-light">B-2129/787</div>
        </div>
        <div class="fd-general__element">
          <div class="fd-general__element__line1 fd-airport">北京</div>
          <div class="fd-general__element__line2 fd-time">18:00</div>
          <div class="fd-general__element__line3 fd-light">计划<span>1500</span></div>
        </div>
      </div>
      <div class="fd-line">
        <div class="fd-line__eobt">EOBT : 15:00</div>
        <div class="fd-line__cobt">COBT : 15:00</div>
        <div class="fd-line__cobt"></div>
        <div class="fd-line__flg">
          <span class="label label-danger">要客</span>&nbsp;
          <span class="label label-primary">特服</span></div>
      </div>
      <div class="fd-related">
        <a class="fd-related__pre">
          <div class="arrow">前序</div>
          <div class="flight">MF8401<br>已经到达</div>
          <div style="flex:1"></div>
        </a>
        <a class="fd-related__aft">
          <div style="flex:1"></div>
          <div class="flight">MF8401<br>已达到达</div>
          <div class="arrow">后序</div>
        </a>
      </div>
      <div class="fd-delay">
        <div class="fd-delay__time">
          延误时间<br>
          <span style="font-size:1rem;">23分</span>
        </div>
        <div class="fd-delay__reason">
          延误原因:旅客<br>
          对外原因:旅客<br>
          原因说明:等待晚到旅客<br>
        </div>
      </div>
      <div class="fd-spt">
        <div class="fd-spt__time">
          <div>上客<br>13:00</div>
        </div>
        <div class="fd-spt__time">
          <div>客齐<br>13:00</div>
        </div>
        <div class="fd-spt__time">
          <div>关货舱<br>13:00</div>
        </div>
        <div class="fd-spt__time">
          <div>关客舱<br>13:00</div>
        </div>
        <div class="fd-spt__time">
          <div>撤轮档<br>13:00</div>
        </div>
      </div>
    </scroller>
    <div class="menus" :style="{ height: menuHeight}">
      <span v-for="(menu,index) in menus" :key="menu.name" class="menu"
                   @click="index==4?showOrHideMenu() : ((menu.url && $router.push($route.params.id + menu.url)) ||'')">
        <span class="icon xa-icon" v-html="menu.icon"></span>
        <span v-html="menu.title"></span>
      </span>
    </div>
  </div>
</template>
<script>
  import footerGuide from '@/components/footer'
  import headTop from '@/components/header'

  var menus = [
    {name: 'support', title: '保障详细', icon: '&#xe737;'},
    {name: 'crew', title: '机组信息',icon: '&#xe679;',url:'/crew'},
    {name: 'route', title: '航线分析', icon: '&#xe70f;',url:'/route'},
    {name: 'release', title: '放行资料', icon: '&#xe72c;'},
    {name: 'more', title: '更多', icon: '&#xe677;'},
    {name: 'business', title: '商务信息', icon: '&#xe70f;',url:'/business'},
    {name: 'passenger', title: '旅客信息',icon: '&#xe737;'},
    {name: 'vip', title: '载重信息', icon: '&#xe737;'},
    {name: 'airport', title: '机场信息', icon: '&#xe737;'},
    {name: 'vip', title: '要客信息', icon: '&#xe737;'},
  ]
  export default {
    data: function () {
      return {
        menus: menus,
        openMenu:false,
        menuHeight:'2rem',
        showModal: false
      }
    },
    methods: {
      refresh: function (done) {
        setTimeout(() => {
          done()
        }, 1500)
      },
      showOrHideMenu:function(tf){
        this.openMenu = tf||!this.openMenu
        this.showModal = this.openMenu
        this.menuHeight = (this.openMenu?  Math.ceil(menus.length/5) * 2:2)+ 'rem'
      }
    },
    components: {
      headTop,
      footerGuide
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  @import "src/style/base.scss";

  .xa-page {
    background-color: #f2f2f2;
  }
  .fd-general {
    width: 100%;
    display: flex;
    padding: 0.3rem;
    text-align: center;
    background-color: #fff;
    border-bottom: 1px solid $XaBorderGray;
    color: $XaColorDarkerBlue;
    z-index: 800;
    &__element {
      flex: 4;
      height: 4.4rem;
      &__line1 {
        height: 1.4rem;
        line-height: 1.4rem;
      }
      &__line2 {
        line-height: 2.0rem;
        height: 2.0rem;
      }
      &__line3 {
        height: 1.0rem;
        line-height: 1.0rem;
      }
    }
    &__element__middle {
      flex: 3;
      height: 4.4rem;
    }
  }

  .fd-line {
    display: flex;
    font-size: .6rem;
    height: 1.4rem;
    line-height: 1.4rem;
    text-align: center;
    &__eobt, &__cobt {
      flex: 1;
    }
    &__flg {
      flex: 1;
    }
  }

  .fd-related {
    height: 2.6rem;
    display: flex;
    border-bottom: 1px solid $XaBorderGray;
    font-size: .6rem;
    background-color: #fff;
    &__pre {
      display: flex;
      flex: 1;
      padding: .3rem .3rem;
      border-right: 1px solid $XaBorderGray;
      & .arrow {
        flex: 1;
        line-height: 2rem;
        text-align: center;
        &:before {
          content: " ";
          @include setArrow(0.4rem, $XaBorderGray, 2px, left);
        }
      }
      & .flight {
        flex: 1;
        line-height: 1rem;
        text-align: left;
      }
    }
    &__aft {
      display: flex;
      flex: 1;
      padding: .3rem .3rem;
      & .arrow {
        flex: 1;
        line-height: 2rem;
        text-align: center;
        &:after {
          content: " ";
          @include setArrow(0.4rem, $XaBorderGray, 2px, right);
        }
      }
      & .flight {
        flex: 1;
        line-height: 1rem;
        text-align: left;
      }
    }
  }

  .fd-content {
    padding: .3rem;
    background-color: #fff;
  }

  .fd-delay {
    display: flex;
    margin-top: .4rem;
    background-color: #fff;
    &__time {
      flex: 2;
      border-right: 1px solid $XaBorderGray;
      padding: .8rem;
      text-align: center;
    }
    &__reason {
      flex: 5;
      padding: .8rem;
    }
  }

  .fd-spt {
    display: flex;
    margin-top: .4rem;
    height: 2.4rem;
    background-color: #fff;
    &__time {
      padding: .4rem 0;
      flex: 1;
      text-align: center;
      & > div {
        border-right: 1px solid $XaBorderGray;
      }
      &:last-child {
        border-right: none;
      }
    }
  }

  .fd-plane {
    position: absolute;
    left: 45%;
    font-size: 1rem;
    height: 1.5rem;
    width: 1.5rem;
    margin-top: -1rem;
    margin-left: -0.5rem;
    color: $XaColorAirBlue;
  }

  .fd-airport {
    font-size: 1.0rem;
    color: $XaColorDarkBlue;
  }

  .fd-time {
    font-size: 1.4rem;
    color: $XaColorDarkerBlue;
  }

  .fd-light {
    font-size: 0.6rem;
    color: $XaTextGrayColor;
  }

  .st {
    color: #c41a16;
  }

  .menus {
    background-color: #fff;
    position: fixed;
    z-index: 1000;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    border-top: 1px solid $XaBorderGray;
    display: flex;
    flex-flow: wrap;
    transition: height 0.2s;
    & .menu {
      width: 20%;
      display: block;
      text-align: center;
      font-size: .45rem;
      & > .xa-icon {
        display: block;
        margin-bottom: .1rem;
        margin-top: .3rem;
        height: .8rem;
        font-size: .8rem;
      }
    }
  }

</style>



