<template>
  <div class="xa-page">
    <head-top back="true" title="MF8401">
    </head-top>
    <scroller style="padding-top:2rem;">
        <div v-for="c in crews" class="crew">
          <div class="container">
            <div style="font-size: 0.8rem;font-weight: bold;color: #212121;line-height: 1.2rem">{{c.cName}}</div>
            <div style="font-size: 0.7rem;color: #616161;line-height: 1.2rem">{{c.rankName}}</div>
            <div style="font-size: 0.6rem;color: #9e9e9e;line-height: 1.2rem">{{c.eName}}</div>
          </div>

          <!--<div class="mainDiv">-->
            <!--<div class="subDiv mainContent" style="flex:5">{{c.rankName}}</div>-->
            <!--<div class="subDiv mainContent" style="flex:4">{{c.cName}}</div>-->
            <!--<div class="subDiv mainContent" style="flex:6;font-size: 0.6rem">{{c.eName}}</div>-->
          <!--</div>-->
          <!--<div class="mainDiv">-->
            <!--<div class="subDiv secondContent" style="flex:5"><a class="telLink" :href="'tel:'+c.mobileNo">{{c.mobileNo}}</a></div>-->
            <!--<div class="subDiv secondContent" style="flex:4">{{c.sex}}</div>-->
            <!--<div class="subDiv secondContent" style="flex:6">{{c.native}}</div>-->
          <!--</div>-->
        </div>
    </scroller>
  </div>
</template>
<script>
  import headTop from '@/components/header'
  export default{
    data(){
      return{
        crews: []
      }
    },
    methods:{

      getFlightCrews:function() {
        this.$http.get('http://www.easy-mock.com/mock/596f1d0ea1d30433d837b4bf/example/crew').then(response => {
          return response.json()
        }).then(json=>{
          this.crews = json
        });
      }
    },
    created: function (){
      this.getFlightCrews()
    },
    components: {
      headTop
    }
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "src/style/base.scss";
  .xa-page {
  }

  .mainDiv{
    /*display: -webkit-box; !important*/
    display: flex;
  }
  .subDiv{
    white-space:nowrap;
    overflow:hidden;
    padding:0 0.2rem 0.2rem;
    text-align: center;
    vertical-align: middle;
    flex-grow:1; //for old version browser
    width:0; //for old version browser
  }

  .mainContent{
    font-size: 0.9rem;
    font-weight: bold;
    line-height: 2rem;
  }

  .secondContent{
    color: #666666;
    font-size: 0.8rem;
    line-height: 1.5rem;
  }
  .crew {
    margin: 0.3rem;
    padding:0.3rem 1rem 0.1rem 1rem;
    border-bottom: 0.1rem solid $XaColorLightBlue;
    display: flex;
    align-items: center;
    border-radius:0.4rem;
    background-color: deepskyblue;
    &:after {
      content: "";
      @include setArrow(0.4rem, $XaBorderGray, 2px, right);
    }
  }

  .container
  {
    flex:1;

  }
  .telLink {
    color:$XaColorAirBlue;
    font-weight: bold;
    text-decoration: none;
  }
</style>



