<template>
  <picker v-bind:data="items" :show="show" v-on:select="_onSelect($event)">

  </picker>
</template>
<script>
  import picker from '@/components/picker/picker'
  export default{
    props: ['show', 'start', 'end'],
    data: function () {
      return {
        items: this._getItems()
      }
    },
    methods: {
      _getItems(){
        var items = []
        const day = 24 * 60 * 60 * 1000;
        var date = new Date(this.start)
        var endDate = new Date(this.end)
        var Y, M
        while (date.getTime() <= endDate.getTime()) {
          var y = date.getFullYear()
          var m = date.getMonth() + 1
          var d = date.getDate()
          date = new Date(date.getTime() + day)
          if (!Y || Y.value != y) {
            M = {
              text: m,
              value: m,
              children: [{
                text: d,
                value: d
              }]
            }
            Y = {
              text: y,
              value: y,
              children: [M]
            }
            items.push(Y)
            continue
          }
          if (M.value != m) {
            M = {
              text: m,
              value: m,
              children: [{
                text: d,
                value: d
              }]
            }
            Y.children.push(M)
            continue
          }
          M.children.push({
            text: d,
            value: d
          })

        }
        return items
      },
      _onSelect(selected){
        var date = new Date(selected[0].value +'-'+selected[1].value +'-'+ selected[2].value)
        this.$emit('select',date)
      }
    },
    components: {
      picker
    }
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>

</style>



