<template>
  <div class="xa-picker">
    <div class="xa-picker__hd">
      <a class="xa-picker__action" @click="cancel">取消</a>
      <a class="xa-picker__action" @click="ok">确定</a>
    </div>
    <div class="xa-picker__bd">
      <!--<group v-for="(items,key) in itemsGroup" :key=key :items="items" v-on:select="onSelect(items,$event,key)">-->

      <!--</group>-->
      <group v-for="(item,index) in items" :key=index :items="item"  v-on:select="onSelect($event,index)">

      </group>
    </div>
  </div>
</template>

<script>
  import group from '@/components/picker/group'
  export default{
    props: ['itemsGroup', 'show','data'],
    data(){
      return {
        items: [],
        depth: 0,
        selected: []
      }
    },
    methods: {
      _getDepth(data){
        var depth = 1
        if (Object.prototype.toString.call(data) == '[object Array]') {
          var dMaxChildren = 0
          data.forEach(d=> {
            dMaxChildren = Math.max(dMaxChildren, d.children ? this._getDepth(d.children) : 0)
          })
        }
        return depth + dMaxChildren
      },
      _generateItems(data,depth){
        if (!data || !data.length) return []
        //如果是不级联的多维数组则直接返回
        if (depth ==1 ) return data
        //多维数组带children
        var items = []
        //按深度依次取出选定的数组,切换的时候可能会出现选定值大于新的数据的长度,这个时候要取更小值
        for(var i=0;i<depth;i++){
          items[i] = data
          var min  =  Math.min(this.selected[i],data.length - 1)
          data = data[min].children
        }
        return items
      },

      ok(){
        var data  = []
        for(var i=0;i<this.selected.length;i++){
          var item = this.items[i][this.selected[i]]
          data.push({
            text:item.text,
            value:item.value
          })
        }
        this.$emit('select', data)
      },
      cancel(){

      },
      onSelect($event,index){
        this.selected[index]=$event
        this.items = this._generateItems(this.data,this.depth)
      }
    },
    watch: {
      show: function (val) {
        this.$el.style.transform = val ? 'translate3d(0,0,0)' : 'translate3d(0,100%,0)'
      }
    },
    mounted(){
      this.depth = this._getDepth(this.data)
      var d = this.depth
      while(d--){
        this.selected.push(0)
      }
      console.log(this.depth)
      this.items = this._generateItems(this.data,this.depth)
    },
    components: {
      group
    },
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>

</style>



