var mockUrl = 'https://www.easy-mock.com/mock/596f1d0ea1d30433d837b4bf/example/'
var baseUrl = ''
if (process.env.NODE_ENV == 'development') {
  baseUrl = 'http://11.4.64.29/'
} else if (process.env.NODE_ENV == 'production') {
  baseUrl = 'http://11.4.64.29/'
}

baseUrl = mockUrl

export default function (url) {
  return baseUrl + url
}



