<template>
  <div>
    <header class='header-nav'>
      <div class="header-left">
        <div class="back" v-if="back||url" @click="back?$router.go(-1):$router.push(url)"></div>
        <slot name="left"></slot>
      </div>
      <div class="header-title">
        {{title}}
      </div>
      <div class="header-right">
        <slot name="right"></slot>
      </div>
    </header>
  </div>
</template>

<script>
  export default {
    data:function(){
      return {}
    },
    props:['back','title','url'],
    methods:{
    }
  }

</script>

<style lang="scss" rel="stylesheet/scss" >
  @import "src/style/base.scss";
  .header-nav{
    color:#fff;
    background-color: $XaColorAirBlue;
    position: fixed;
    z-index: 100;
    left: 0;
    width:100%;
    height:$XaHeaderHeight
  }
  .header-left{
    position:absolute;
    left:0;
    top:0;
    width:2rem;
    height: $XaHeaderHeight;
    line-height: $XaHeaderHeight;
    text-align:center;
    & .back:after{
      content: " ";
      @include setArrow(0.4rem, #fff, 2px,left);
    }
  }
  .header-right{
    position:absolute;
    right:0;
    top:0;
    width:2rem;
    height: $XaHeaderHeight;
    line-height: $XaHeaderHeight;
    text-align:center;
  }
  .header-title{
    width:100%;
    height: $XaHeaderHeight;
    line-height: $XaHeaderHeight;
    font-size:.7rem;
    color:#fff;
    text-align: center;
  }
</style>



